.program-type {
  margin-top: 20px;
  margin-bottom: 20px;
}

.program-type__list {
  .zeroing-the-list();
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 20px;

  @media (min-width: @tablet-width) {
    gap: 30px;
  }

  @media (min-width: @desktop-width) {
    flex-direction: row;
    flex-wrap: wrap;
    gap: 80px;
  }
}

.program-type__item {
  background-color: #f2f2f2;

  @media (min-width: @desktop-width) {
    width: 570px;
  }
}

.program-type__title {
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
}

.program-type__button {
  background-color: transparent;
  color: @basic-black;
  line-height: 16px;
}
